@import url('https://fonts.googleapis.com/css?family=Chewy|Slabo+27px');

body {
  margin: 0;
  padding: 0;
  font-family: 'Slabo 27px', serif;
  background: -webkit-radial-gradient(#fef8f5 15%, rgba(0,0,0,0) 16%), -webkit-radial-gradient(#f4fbfe 15%, rgba(0,0,0,0) 16%), #fff;
  background: -o-radial-gradient(#fef8f5 15%, rgba(0,0,0,0) 16%), -o-radial-gradient(#f4fbfe 15%, rgba(0,0,0,0) 16%), #fff;
  background: radial-gradient(#fef8f5 15%, rgba(0,0,0,0) 16%), radial-gradient(#f4fbfe 15%, rgba(0,0,0,0) 16%), #fff;
    background-position: 0 0, 30px 30px;
    background-origin: padding-box;
    background-clip: border-box;
    background-size: 60px 60px;
}

.animateSpin {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 10000ms;
          animation-duration: 10000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear; 
  -webkit-transform-origin: center center; 
      -ms-transform-origin: center center; 
          transform-origin: center center;
}

@-webkit-keyframes spin {
    from {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@keyframes spin {
    from {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@-webkit-keyframes heartbeat {
  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
	}
}

@keyframes heartbeat {
  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
	}
}


